import { BackendComponent } from '../backend/backend/backend.component';
import { DomainComponent } from '../backend/basic.configuration/domain/domain.component';
import { Routes } from '@angular/router';
import { InterceptorComponent } from '../backend/basic.configuration/interceptor/interceptor.component';
import { ExceptionComponent } from '../backend/basic.configuration/exception/exception.component';
import { EhcacheComponent } from '../backend/plug/ehcache/ehcache.component';
import { DubboComponent } from '../backend/plug/dubbo/dubbo.component';
import { ShiroComponent } from '../backend/plug/shiro/shiro.component';
import { RabbitmqComponent } from '../backend/plug/rabbitmq/rabbitmq.component';

export const backendRouter : Routes =[
{
    path: 'backend',
    component: BackendComponent,
    children:[
        {path:'cross/domain',component:DomainComponent},
        {path:'interceptor',component:InterceptorComponent},
        {path:'exception',component:ExceptionComponent},
        {path:'ehcache',component:EhcacheComponent},
        {path:'shiro',component:ShiroComponent},
        {path:'dubbo',component:DubboComponent},
        {path:'rabbitmq',component:RabbitmqComponent}
    ]
  }
]