import { Component, OnInit } from '@angular/core';
import { DataService } from '../../data.service'
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-backend',
  templateUrl: './backend.component.html',
  styleUrls: ['./backend.component.scss']
})
export class BackendComponent implements OnInit {

  private menus : any;
  private nowTime;
  private defimg : boolean = true;

  constructor(private data : DataService,private router : Router,private route : ActivatedRoute) { }

  ngOnInit() {
    this.menus = this.data.getMenus();
    this.getNowTime();
    this.route.queryParams.subscribe( queryParams =>{
      if (queryParams.showDef)this.defimg = false;
    })
  }

  getNowTime(){
    let week = this.data.getNowWeek();
    setInterval( () =>{
      let time = new Date().toLocaleString();
      this.nowTime = week.concat(' ').concat(time);
     })
  }

  selectContent(path){
    this.defimg = false;
    this.router.navigate(['backend/'.concat(path)]);
  }


}
