import { Component, OnInit } from '@angular/core';
import { DataService } from '../../data.service'
import { Router } from '@angular/router';

@Component({
  selector: 'app-frontend',
  templateUrl: './frontend.component.html',
  styleUrls: ['./frontend.component.scss']
})
export class FrontendComponent implements OnInit {

  private menus : any;
  private menuId : any;
  private showLib : boolean = false;
  private showContent : boolean = false;
  private nowDay = new Date().getDate();
  private nowTime ;

  constructor(private data : DataService,private router : Router) { }

  private bsValue = new Date();
  private bsRangeValue: Date[];
  private maxDate = new Date();
  private calender :  Array<Array<any>>;//日历数组

  ngOnInit() {
    this.menus = this.data.getMenus();
    this.maxDate.setDate(this.maxDate.getDate() + 7);
    this.bsRangeValue = [this.bsValue, this.maxDate];
    this.calender = this.data.getCalender();
    this.getNowTime();
  }

  private time;

  openSonMenu(id){
    this.menuId = id;
    this.showLib = true;
    this.showContent = false;
  }

  openContent(path){
    this.showLib = false;
    this.showContent = true;
    this.router.navigate(['frontend/'.concat(path)]);
  }

  getNowTime(){
    let week = this.data.getNowWeek();
    setInterval( () =>{
      let time = new Date().toLocaleString();
      this.nowTime = week.concat(' ').concat(time);
     })
  }

}
