import { Routes } from '@angular/router';
import { FrontendComponent } from '../frontend/frontend/frontend.component';
import { NgcreateComponent } from '../frontend/ngcreate/ngcreate.component';
import { NguseComponent } from '../frontend/nguse/nguse.component';
import { VuecreateComponent } from '../frontend/vuecreate/vuecreate.component';
import { VueuseComponent } from '../frontend/vueuse/vueuse.component';

export const frontendRouter : Routes = [
    {
        path: 'frontend',
        component: FrontendComponent,
        children: [
            {path:'ngcreate',component:NgcreateComponent},
            {path:'nguse',component:NguseComponent},
            {path:'vuecreate',component:VuecreateComponent},
            {path:'vueuse',component:VueuseComponent}
        ]
      }
]